*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;

}
.main_div{
  width: 100%;
  height: 100vh;
  background: #badc58;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: center;

}
.center_div{
      width: 25%;
      height: 60vh;
      background-color: #f4f4f4;
      box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
}
h1{
  color: #fff;
  background: transparent;
  font-size: 50px;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  width: 100%;
  box-shadow: 5px 5px 15px -5 rgba(0 ,0,0,0.3);
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
}
button{
  min-height: 40px;
  /* width: 40px; */
  border-radius: 5%;
  border-color: transparent;
  background-color: #30336b;
  color: #fff;
  font-size: 20px;
  border: none;
  outline: none;
  margin-top: 30px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3 );
}
button:hover{
  background-color: #1da460;
  
}
btn_div{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
button{
  width: 100%;
}
ol li{
  padding: 0px;
  text-align: left;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.todo_style .fa-times-circle{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 15px -5px  rgba0(0,0,0,0.3);
  ;
}
.todo_style .fa-times:hover{
  background-color: crimson;
}
.todo_style .fa-times:hover .list_style{
  
  color:crimson;
  }

@media (max-width: 768px){
  .center_div{
    width: 55%;
  }
  }
 .todo_style .fa-times{
  margin:0 15px 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;

 }
 button{
  margin-left: 5px;
  min-height: 40px;
  width: 120px;
  color: #fff;
  font-size: 30px;
 }
